* { font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif; }
body { padding: 3rem; }
input { width: 100%; font-size: 1.2rem; color: grey; padding: .5rem; }
.main-grid { display: grid; gap: 1rem; grid-template-columns: repeat(auto-fit, minmax(15rem, 1fr)); }
.card { align-content: center; border: 1px solid gray; border-radius: 6px; overflow: hidden; }
.card__image { width: 100%; height: 10rem; object-fit: cover; object-position: center; display: block; }
.card__caption { margin: 0; padding: .5rem; text-align: center; font-weight: 700; font-size: .9rem; text-transform: uppercase; }